import React from 'react'
import { Route, useRouteMatch } from 'react-router-dom'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Heading } from '@pancakeswap-libs/uikit'
import { BLOCKS_PER_YEAR } from 'config'
import orderBy from 'lodash/orderBy'
import partition from 'lodash/partition'
import useI18n from 'hooks/useI18n'
import useBlock from 'hooks/useBlock'
import { getBalanceNumber } from 'utils/formatBalance'
import { useFarms, usePriceBnbBusd, usePools, useFegPrice, useQuaPrice } from 'state/hooks'
import { QuoteToken, PoolCategory } from 'config/constants/types'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import Coming from './components/Coming'
import PoolCard from './components/PoolCard'
import PoolTabButtons from './components/PoolTabButtons'
import Divider from './components/Divider'

const Farm: React.FC = () => {
  const { path } = useRouteMatch()
  const TranslateString = useI18n()
  const { account } = useWallet()
  const farms = useFarms()
  const pools = usePools(account)
  const fegPrice = useFegPrice()
  const quaPrice = useQuaPrice()
  const bnbPriceUSD = usePriceBnbBusd()
  const block = useBlock()

  const priceToBnb = (tokenName: string, tokenPrice: BigNumber, quoteToken: QuoteToken): BigNumber => {
    const tokenPriceBN = new BigNumber(tokenPrice)
    if (tokenName === 'BNB') {
      return new BigNumber(1)
    }
    if (tokenPrice && quoteToken === QuoteToken.BUSD) {
      return tokenPriceBN.div(bnbPriceUSD)
    }
    return tokenPriceBN
  }

  let poolsWithApy = pools.map((pool) => {
    const isBnbPool = pool.poolCategory === PoolCategory.BINANCE
    const rewardTokenFarm = farms.find((f) => f.tokenSymbol === pool.tokenName)
    const stakingTokenFarm = farms.find((s) => s.tokenSymbol === pool.stakingTokenName || `${s.tokenSymbol}-${s.quoteTokenSymbol}` === pool.stakingTokenName)
    const stakingTokenPriceInUSD = isBnbPool ? new BigNumber(1) : new BigNumber(stakingTokenFarm?.tokenPriceVsQuote).times(stakingTokenFarm?.quoteTokenPrice)
    // const rewardTokenPriceInBNB = priceToBnb(
    //   pool.tokenName,
    //   rewardTokenFarm?.tokenPriceVsQuote,
    //   rewardTokenFarm?.quoteTokenSymbol,
    // )
    let rewardTokenPriceInUsd = new BigNumber(0)

    if (pool.tokenName === 'FEG') {
      rewardTokenPriceInUsd = new BigNumber(fegPrice)
    } else if (pool.tokenName === 'QUA') {
      rewardTokenPriceInUsd = new BigNumber(quaPrice)
    } else {
      rewardTokenPriceInUsd = new BigNumber(rewardTokenFarm?.tokenPriceVsQuote).times(rewardTokenFarm?.quoteTokenPrice)
    }

    const totalRewardPricePerYear = rewardTokenPriceInUsd.times(pool.tokenPerBlock).times(BLOCKS_PER_YEAR)
    const totalStakedAmountInUSD = stakingTokenPriceInUSD.times(getBalanceNumber(pool.totalStaked, pool.stakingTokenDecimals))
    const userStakedAmountInUSD = stakingTokenPriceInUSD.times(getBalanceNumber(pool.userData?.stakedBalance, pool.stakingTokenDecimals))

    const apy = totalRewardPricePerYear.div(totalStakedAmountInUSD).times(100)

    return {
      ...pool,
      isFinished: pool.sousId === 0 ? false : pool.isFinished || block > pool.endBlock,
      apy,
      userStakedAmountInUSD,
      totalStakedAmountInUSD
    }
  })

  poolsWithApy = poolsWithApy.sort((pool1, pool2) => {
    if (pool1.sousId === 13) {
      return -1
    }
    if (pool2.sousId === 13) {
      return 1
    }
    if (pool1.apy > pool2.apy) {
      return -1
    }
    return 1
  })

  const [finishedPools, openPools] = partition(poolsWithApy, (pool) => pool.isFinished)

  return (
    <Page>
      <Heading as="h1" size="xl" color="primary" mb="10px" style={{ textAlign: 'center' }}>
        {TranslateString(999, 'Pools')}
      </Heading>
      <Heading as="h1" size="lg" color="#9700ff" mb="50px" style={{ textAlign: 'center' }}>
        {TranslateString(999, 'Stake TMDS to earn rewards.')}
      </Heading>
      <PoolTabButtons />
      <Divider />
      <FlexLayout>
        <Route exact path={`${path}`}>
          <>
            {orderBy(openPools, ['sortOrder']).map((pool) => (
              <PoolCard key={pool.sousId} pool={pool} />
            ))}
            {/* <Coming /> */}
          </>
        </Route>
        <Route path={`${path}/history`}>
          {orderBy(finishedPools, ['sortOrder']).map((pool) => (
            <PoolCard key={pool.sousId} pool={pool} />
          ))}
        </Route>
      </FlexLayout>
      <div style={{ maxWidth: "100%", textAlign: 'center', marginTop: '25px' }}>
        <BannerImage src="/images/egg/8.png" alt="illustration" />
      </div>
    </Page>
  )
}

const Hero = styled.div`
  align-items: center;
  color: ${({ theme }) => theme.colors.primary};
  display: grid;
  grid-gap: 32px;
  grid-template-columns: 1fr;
  margin-left: auto;
  margin-right: auto;
  max-width: 250px;
  padding: 48px 0;
  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 16px;
    li {
      margin-bottom: 4px;
    }
  }
  img {
    height: auto;
    max-width: 100%;
  }
  @media (min-width: 576px) {
    grid-template-columns: 1fr 1fr;
    margin: 0;
    max-width: none;
  }
`

const BannerImage = styled.img`
  max-width: 300px;
  height: auto;
`

export default Farm
